import React from "react";

import PropTypes from "prop-types";

import axios from "axios";
import qs from "qs"; //post to php w/o file_get_contents('php://input');

import { Loader } from "semantic-ui-react";

export default class ContactCheck extends React.Component {
  //checks server for contact (login) and specific configuration

  static propTypes = {
    appState: PropTypes.object.isRequired,
    appStateUpdate: PropTypes.func.isRequired,
    callURL: PropTypes.string.isRequired,
  };

  constructor(props) {
    super(props);

    this.devMode = props.appState.devMode;

    this.serverCalled = false;
    this.axiosConfig = {
      withCredentials: true,
      credentials: "include",
      timeout: 5000,
      headers: {
        Accept: "application/json",
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    };

    this.state = {
      info: "Checking login and contact info ...",
      error: false,
    };
  }

  serverCall() {
    const { props } = this;
    const self = this;

    const urlParams = new URLSearchParams(window ? window.location.search : {});
    var content_id = urlParams.get("content"); //ticket for content
    var creator_id = urlParams.get("creator"); //ticket for a creator
    var site_id = urlParams.get("site"); //ticket from a site

    var ticket_id = urlParams.get("ticket"); //ticket id number
    var form = urlParams.get("form"); //form name
    var department = urlParams.get("department"); //department name

    //JS embed parameters
    const vwParams = window.VideoWhisperSupport;
    if (vwParams) {
      if (vwParams.creator) if (!creator_id) creator_id = vwParams.creator;
      if (vwParams.content) if (!content_id) content_id = vwParams.content;
      if (vwParams.site) if (!site_id) site_id = vwParams.site;
      if (vwParams.form) if (!form) form = vwParams.form;
      if (vwParams.department) if (!department) department = vwParams.department;
      if (vwParams.ticket) if (!ticket_id) ticket_id = vwParams.ticket;
    }

    const params = {
      creator_id: creator_id,
      content_id: content_id,
      site_id: site_id,
      ticket_id: ticket_id,
      form: form,
      department: department,
    };

    if (this.devMode)
      console.log(
        "VideoWhisper/screens/contactCheck serverCall",
        props.callURL,
        params,
        vwParams
      );

    axios.defaults.withCredentials = true;
    axios
      .post(props.callURL, qs.stringify(params), this.axiosConfig)
      .then(function (response) {
        self.serverProcess(response);
      })
      .catch(this.serverError.bind(this));

    this.serverCalled = true;
  }

  serverProcess(response) {
    if (this.devMode)
      console.log(
        "VideoWhisper/screens/contactCheck serverProcess:",
        response,
        this.state
      );

    //ajax
    if (!response.data) {
      this.props.appStateUpdate(
        { contactCheck: true, session: { login: 0, error: 1 } },
        { screen: true }
      );
    } else {
      if (response.data.contact)
        this.props.appStateUpdate({
          contact: response.data.contact,
          confirm: response.data.contact.pin,
          contact_id: response.data.contact.id,
        });

      if (response.data.session)
        this.props.appStateUpdate(
          {
            contactCheck: true,
            session: response.data.session,
            config: response.data.config,
            language: response.data.config.language, 
            flag: response.data.config.flag,
          },
          { screen: true }
        );

      if (response.data.balance)
        this.props.appStateUpdate({ balance: response.data.balance });

      if (response.data.error)
        this.setState({ error: true, info: response.data.info });
    }
  }

  serverError(error) {
    console.log("VideoWhisper/screens/contactCheck serverError:", error);

    this.props.appStateUpdate(
      { contactCheck: true, session: { login: 0, error: 1 } },
      { screen: true }
    );
  }

  render() {
    this.devMode = this.props.appState.devMode;

    if (!this.serverCalled) this.serverCall();

    return (
      <div className="layoutFull">
        <Loader active inline className="slow red" />
        <br />
        {this.state.info}
      </div>
    );
  }
}
